import api from '../../api/index'
import EditorBar from './wangeditor'
let p = document.createElement('p')
let a = document.createElement('a')
let div = document.createElement('div')
export default {
    name:'policy',
    data(){
        return{
            uploadUrl:this.GLOBAL.BASE_URL+'/api/Administration/group/UploadFile',
            fileList:[],
            image:'',
            titleInfo:'',
            content: ``,
            editor: {
                info: ''
            },
            isClear: false,
        }
    },
    methods: {
         // 上传文件
        handleSuccess(response,file, fileList){
            //上传成功要处理的事
            console.log(file.name)
                response.data[0] == ''?
                this.$message({
                    showClose: true,
                    message: '上传失败',
                    type: 'warning'
                }):
                this.$message({
                    showClose: true,
                    message: '上传成功',
                    type: 'success'
                })
            p.append(a)
            a.className = 'download'
            a.innerText = file.name;
            a.href = response.data[0];
            a.download = file.name;
            div.append(p)
            this.editor.info += div.innerHTML;
        },
        uploadPhoto(e) {
            let _this = this;
            // 利用fileReader对象获取file
            var file = e.target.files[0];
            var filesize = file.size;
            var filename = file.name;
            // 2,621,440   2M
            if (filesize > 2101440) {
                // 图片大于2MB
               this.$message({
                    showClose: true,
                    message: '图片不能超过2M',
                    type: 'warning'
                })
            }
            var reader = new FileReader();
            reader.readAsDataURL(file);
            reader.onload = (e)=> {
                // 读取到的图片base64 数据编码 将此编码字符串传给后台即可
                _this.image = e.target.result;
                console.log(this.image);  
            }
        },
        saveHtml:function(event){
            let _this = this;
            console.log(this.image)
            if(this.titleInfo == '' || this.editor.info == ''){
                this.$message({
                    showClose: true,
                    message: '标题或内容不能为空',
                    type: 'warning'
                })
                return;
            }
            console.log(this.editor.info)
            let publicPage = {
                pic:_this.image,
                title:_this.titleInfo,
                content:_this.editor.info
            }
            console.log(publicPage.content)
            api.AddOfficial(publicPage).then(res=>{
                console.log(res)
                this.$message({
                    showClose: true,
                    message: '保存成功',
                    type: 'success'
                })
                this.titleInfo = '';
                this.image = '';
                this.editor.info = '';
                this.$router.push({
                    path:'/policyList'
                })
                location.reload()
            }) 
        },
        change (val) {
        this.editor.info1 = val
        }
    },
    components: {
        EditorBar
    }
}



